// src/sections/home/ProductSlider.tsx
import Slider from "react-slick";
import { Stack, Typography, useTheme } from "@mui/material";
import { ProductCard } from "@app/components";
import { useNavigate } from "react-router-dom";
import { PATH_DASHBOARD } from "@app/routes/paths";

interface ProductSliderProps {
  data: any[];
}

const ProductSlider = ({ data }: ProductSliderProps) => {
  const theme = useTheme();
  const navigate = useNavigate();

  // sirf 10 products
  const products = data?.slice(0, 10) || [];

  const settings = {
    infinite: true,
    speed: 2000,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2500,
    arrows: false,
    responsive: [
      {
        breakpoint: 1536,
        settings: { slidesToShow: 4, slidesToScroll: 1, infinite: true },
      },
      {
        breakpoint: 1280,
        settings: { slidesToShow: 3, slidesToScroll: 1, infinite: true },
      },
      {
        breakpoint: 1024,
        settings: { slidesToShow: 2, slidesToScroll: 1, infinite: true },
      },
      {
        breakpoint: 768,
        settings: { slidesToShow: 1, slidesToScroll: 1, infinite: true },
      },
      {
        breakpoint: 480,
        settings: { slidesToShow: 1, slidesToScroll: 1, infinite: true },
      },
    ],
  };

  return (
    <Stack sx={{ width: "100%", py: 5 }}>
      <Typography
        variant="h4"
        textAlign="center"
        gutterBottom
        sx={{
          fontWeight: 700,
          textDecoration: "underline 2px solid",
          textDecorationColor: theme.palette.primary.main,
          textUnderlineOffset: "6px",
          mb: 3,
        }}
      >
        Featured Products
      </Typography>

      <Stack
        sx={{
          height: 400,
        }}
      >
        <Slider {...settings}>
          {products.map((item: any) => (
            <Stack
              key={item.id}
              sx={{ px: 1, alignSelf: { xs: "center", md: "auto" } }}
            >
              <ProductCard
                id={item.id}
                isPrice={false}
                name={item.name}
                category={item.category}
                description={item.description}
                image={item.image}
                price={item.price}
                onViewMore={() =>
                  navigate(`${PATH_DASHBOARD.productDetail}/${item.id}`)
                }
              />
            </Stack>
          ))}
        </Slider>
      </Stack>
    </Stack>
  );
};

export { ProductSlider };
